import cmdAvatar from "@/components/cmd-avatar.vue";
import uniIcons from  "@/components/uni-icons/uni-icons.vue"

import tuiIcon from "@/uni-store/components/icon/icon"
import tuiButton from "@/uni-store/components/extend/button/button"
import tuiListCell from "@/uni-store/components/list-cell/list-cell"
import tuiDivider from "@/uni-store/components/divider/divider"
import tuiLoadmore from "@/uni-store/components/loadmore/loadmore"

var db = wx.cloud.database().collection("userInfo")  
export default {
	components:{cmdAvatar, uniIcons,	
		tuiIcon,
		tuiButton,
		tuiListCell,
		tuiDivider,
		tuiLoadmore},
	data() {
		return {
			height: 64, //header高度
			top: 5, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,
			
			personInfo :[{
				nickName:"",  // 昵称
				age:"",			
				sex:"",  
				career:"",
				schoolData:'',
				provinceSchool:"",
				longitude:"", /* 用户当前位置*/
				latitude:"",
				location:"",
			}],
			
			title: 'Hello',
			ColorList: this.ColorList,
			avatar: [
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
			],
			cuIconList: [{
				cuIcon: 'cardboardfill',
				color: 'red',
				badge: 120,
				name: 'VR'
			}, {
				cuIcon: 'recordfill',
				color: 'orange',
				badge: 1,
				name: '录像'
			}, {
				cuIcon: 'picfill',
				color: 'yellow',
				badge: 0,
				name: '图像'
			}, {
				cuIcon: 'noticefill',
				color: 'olive',
				badge: 22,
				name: '通知'
			}, {
				cuIcon: 'upstagefill',
				color: 'cyan',
				badge: 0,
				name: '排行榜'
			}, {
				cuIcon: 'clothesfill',
				color: 'blue',
				badge: 0,
				name: '皮肤'
			}, {
				cuIcon: 'discoverfill',
				color: 'purple',
				badge: 0,
				name: '发现'
			}, {
				cuIcon: 'questionfill',
				color: 'mauve',
				badge: 0,
				name: '帮助'
			}, {
				cuIcon: 'commandfill',
				color: 'purple',
				badge: 0,
				name: '问答'
			}, {
				cuIcon: 'brandfill',
				color: 'mauve',
				badge: 0,
				name: '版权'
			}],
			modalName: null,
			gridCol:4,
			gridBorder: true,
			menuBorder: true,
			menuArrow: true,
			menuCard: true,
			skin: true,
			listTouchStart: 0,
			listTouchDirection: null,
		}
	}, 
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	},
	
	onLoad() {
		// 读取数据库 获取个人信息
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		
		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.scrollH = res.windowWidth
			}
		})
		
	}, 
	onShow() {
		console.log("有没有执行onshow")
		this.getUserInfo()
	},
	
	methods: {
		tempNavgator(){
			uni.redirectTo({
				url:"../../tableList/register/register"
			})
		},
		getUserInfo(){  
			console.log("用户openid ",global.globalData.openid)
			db.where({
				_openid:global.globalData.openid
			}).get({
				success: this.handleGetUserInfoSucc.bind(this)
			}) 
		},
		handleGetUserInfoSucc(res){
			console.log("获取的数据为 ",res, res.data[0])
			const te = res.data	
			const personInfo = te.map((value, index)=>{
				return{
					nickName:value.nickName,  // 昵称
					age:value.age,			
					sex:value.sex,  
					career:value.career,
					schoolData:value.schoolData,
					provinceSchool:value.provinceSchool,
					longitude:value.longitude, /* 用户当前位置*/
					latitude:value.latitude,
					location:value.location,
				}
			})
			this.personInfo = personInfo ;
			console.log("tap5 用户信息", this.personInfo)			
		},
		fail(err){
			console.log("tap5 用户信息失败。。。",err)
		}
	}
}
